import * as React from "react";
import Link from "gatsby-link";
import {Card, Comment, Container, Grid, Segment} from "semantic-ui-react";
import {ImageSharp, MarkdownRemarkConnection} from "../graphql-types";
import BlogTitle from "../components/BlogTitle";
import TagsCard from "../components/TagsCard/TagsCard";
import BlogPagination from "../components/BlogPagination/BlogPagination";
import Image from "semantic-ui-react/dist/commonjs/elements/Image/Image";

interface BlogProps {
  data: {
    tags: MarkdownRemarkConnection;
    posts: MarkdownRemarkConnection;
  };
  pathContext: {
    tag?: string; // only set into `templates/tags-pages.tsx`
  };
  location: {
    pathname: string;
  };
}

export default (props: BlogProps) => {
  const tags = props.data.tags.group;
  const posts = props.data.posts ? props.data.posts.edges : [];
  const {pathname} = props.location;
  const pageCount = Math.ceil(props.data.posts ? props.data.posts.totalCount : 0 / 10);

  // TODO export posts in a proper component
  const Posts = (
    <Container>
      {posts.map(({node}) => {
        const {frontmatter, timeToRead, fields: {slug}, excerpt} = node;
        const avatar = frontmatter.author.avatar.children[0] as ImageSharp;
        const cover = frontmatter.image.children[0] as ImageSharp;

        const extra = (
          <Comment.Group>
            <Comment>
              <Image size="tiny" shape="circular" inline>
                <img src={avatar.responsiveResolution.src}
                     srcSet={avatar.responsiveResolution.srcSet}/>
              </Image>
              <Comment.Content>
                <Comment.Author style={{fontWeight: 400}}>
                  {frontmatter.author.id}
                </Comment.Author>
                <Comment.Metadata style={{margin: 0}}>
                  {frontmatter.updatedDate} - {timeToRead} min read
                </Comment.Metadata>
              </Comment.Content>
            </Comment>
          </Comment.Group>
        );

        const description = (
          <Card.Description>
            {excerpt}
            <br/>
            <Link to={slug}>Read more</Link>
          </Card.Description>
        );

        return (
          <Card key={slug}
                fluid
                image={<Image><img src={cover.responsiveResolution.src}
                                   srcSet={cover.responsiveResolution.srcSet}/></Image>}
                header={(<h2><Link to={slug}>{frontmatter.title}</Link></h2>)}
                extra={extra}
                description={description}
          />
        );
      })}
    </Container>
  );

  return (
    <Container>
      {/* Title */}
      <BlogTitle/>

      {/* Content */}
      <Segment vertical>
        <Grid padded style={{justifyContent: "space-around"}}>
          <div style={{maxWidth: 600}}>
            {Posts}
            <Segment vertical textAlign="center">
              <BlogPagination Link={Link} pathname={pathname} pageCount={pageCount}/>
            </Segment>
          </div>
          <div>
            <TagsCard Link={Link} tags={tags} tag={props.pathContext.tag}/>
          </div>
        </Grid>
      </Segment>
    </Container>
  );
};

export const pageQuery = graphql`
    query PageBlog {
        # Get tags
        tags: allMarkdownRemark(filter: {frontmatter: {draft: {ne: true}}}) {
            group(field: frontmatter___tags) {
                fieldValue
                totalCount
            }
        }

        # Get posts
        posts: allMarkdownRemark(
            sort: { order: DESC, fields: [frontmatter___updatedDate] },
            filter: {
                frontmatter: { draft: { ne: true } },
                fileAbsolutePath: { regex: "/steps/" }
            },
            limit: 10
        ) {
            totalCount
            edges {
                node {
                    excerpt
                    timeToRead
                    fields {
                        slug
                    }
                    frontmatter {
                        title
                        updatedDate(formatString: "DD MMMM, YYYY")
                        image {
                            children {
                                ... on ImageSharp {
                                    responsiveResolution(width: 700, height: 100) {
                                        src
                                        srcSet
                                    }
                                }
                            }
                        }
                        author {
                            id
                            avatar {
                                children {
                                    ... on ImageSharp {
                                        responsiveResolution(width: 35, height: 35) {
                                            src
                                            srcSet
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
`;



